import React, { useEffect, useState } from "react";
import "../kakaoMap/kakaoMap.css";
import MainSlider from "../components/MainSlider";
import axiosInstance from "../utils/axios";
import { toast } from "react-toastify";

const { kakao } = window;
//    
var imageSrc = "./images/marker.svg";
var imageSize = new kakao.maps.Size(40, 42);
var imageOption = { offset: new kakao.maps.Point(20, 42) }; //  .
function Kakao_main({ indexPet }) {
  const [map, setMap] = useState(null); // map
  const [userLocation, setUserLocation] = useState(null); //   
  const [dragMapCenter, setDragMapCenter] = useState(); //   
  const [isLocationAvailable, setLocationAvailable] = useState(false); //     
  const [isDrag, setIsDrag] = useState(false); //
  const [isGeolocaation, setIsGeolocation] = useState(false); //
  const [circles, setCircles] = useState([]); // 
  const [markers, setMarkers] = useState([]); // 
  const [currentLocationMarker, setCurrentLocationMarker] = useState(null); //     
  const [markersInitialized, setMarkersInitialized] = useState(false);

  useEffect(() => {
    if (!map) {
      mapscript();
    } else {
      getGeolocation(); //  

      getDragLocation(); //  
    }
  }, [map]);

  const mapscript = () => {
    console.log("mapscript");
    var mapContainer = document.getElementById("map"),
      mapOption = {
        center: new kakao.maps.LatLng(37.4800384, 126.8842496), //  
        level: 5, //   
        mapTypeId: kakao.maps.MapTypeId.ROADMAP, // 
      };

    const map = new kakao.maps.Map(mapContainer, mapOption);
    setMap(map);
  };

  //  
  const getGeolocation = () => {
    console.log("geolocation ");
    if (navigator.geolocation) {
      var options = {
        //  ,   ( , GPS )   true 
        enableHighAccuracy: true,

        //        ?
        timeout: 15000, // 15   .
      };

      // GeoLocation    
      navigator.geolocation.getCurrentPosition(success, error, options);

      //
      async function success(position) {
        setLocationAvailable(true); // 
        const time = new Date(position.timestamp); // 
        const coordinates = {
          lon: position.coords.longitude,
          lat: position.coords.latitude,
        };
        setUserLocation(coordinates); //  
        console.log(coordinates);

        const latlng = new kakao.maps.LatLng(
          position.coords.latitude,
          position.coords.longitude
        );
        map.setCenter(latlng); //       
        console.log(` : ${time} `);

        var locPosition = new kakao.maps.LatLng(latlng.Ma, latlng.La), //    geolocation   
          messageMarker = '<div style="padding:5px;"> ?!</div>'; //   

        setIsGeolocation(false);
        displayMarker(locPosition, messageMarker);
      }

      //
      function error(error) {
        setLocationAvailable(false); //  
        console.log("geolocation " + error.code + ":" + error.message);
        toast.info("GPS   ", {
          position: "bottom-right",
          autoClose: 2000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        });
      }
    } else {
      // HTML5 GeoLocation         
      var locPosition = new kakao.maps.LatLng(33.450701, 126.570667), //geolocation  
        message = "  geolocation  ..";

      //   
      displayMarker(locPosition, message);
    }
  };

  //  
  const getDragLocation = () => {
    console.log("  ");

    //   
    kakao.maps.event.addListener(map, "dragend", function () {
      // setMarkers(null);
      // setIsDrag(true);
      //   
      var latlng = map.getCenter();

      setDragMapCenter([latlng.Ma, latlng.La]); //  

      document.querySelector(".dragTarget").style.display = "block";
    });
  };

  //       
  async function findDragLoc() {
    try {
      console.log("    ");
      // console.log("@@@@@@@@@@", dragMapCenter);
      const body = {
        lon: dragMapCenter[1],
        lat: dragMapCenter[0],
      };
      const res = await axiosInstance.post("/index/geolocation/drag", body);
      console.log("   ", res.data);
      setCircles(res.data.circles);
      clearMarkers();
      initializeMarkers(map);
    } catch (error) {
      console.log(error);
    }
  }

  //      
  async function findCircle() {
    console.log("    ");
    const body = userLocation;
    // const body = {
    //   //
    //   lon: 126.943232,
    //   lat: 37.5062528,
    // };
    console.log(body);
    try {
      const res = await axiosInstance.post("/index/geolocation", body);
      console.log("geoLoc  ", res.data);
      setCircles(res.data.circles || []);
      clearMarkers();
      initializeMarkers(map);
    } catch (error) {
      console.log(error);
    }
  }

  //   
  useEffect(() => {
    console.log("   ");
    async function findCircle() {
      const body = userLocation;

      console.log(body);
      try {
        const res = await axiosInstance.post("/index/geolocation", body);
        console.log(res.data);
        setCircles(res.data.circles);
        setMarkersInitialized(true);
        initializeMarkers(map);
      } catch (error) {
        console.log(error);
      }
    }
    findCircle();
  }, []);

  useEffect(() => {
    initializeMarkers(map);
  }, [markersInitialized, circles, map]);

  //  
  const initializeMarkers = (map) => {
    console.log("  ");
    console.log(circles);

    // clearMarkers();
    const newMarkers = [];
    if (circles && circles.length > 0) {
      //    
      for (let i = 0; i < circles.length; i++) {
        let latlng = new kakao.maps.LatLng(
          circles[i].startLoc.coordinates[1],
          circles[i].startLoc.coordinates[0]
        );
        console.log(latlng);
        const marker = addMarker(latlng, map, imageSrc, imageSize, imageOption);
        newMarkers.push(marker);
      }
      setMarkers(newMarkers);
    }
  };

  //   
  const clearMarkers = () => {
    if (markers) {
      markers.forEach((marker) => {
        if (marker && marker.setMap) {
          marker.setMap(null);
        }
      });
      setMarkers([]); // markers  
    }
  };

  // 
  const addMarker = (latlng, map, imageSrc, imageSize, imageOption) => {
    if (!map) return;
    console.log(" (addMarker)");

    //   
    var markerImage = new kakao.maps.MarkerImage(
      imageSrc,
      imageSize,
      imageOption
    );
    //  
    var marker = new kakao.maps.Marker({
      map: map, //   
      position: latlng, //   
      image: markerImage, //  
    });
    console.log("  ");
    return marker; //   
  };

  //   
  const myLocation = () => {
    console.log(" ");
    clearMarkers();
    document.querySelector(".dragTarget").style.display = "none";
    setIsGeolocation(true);
    setIsDrag(false);
    getGeolocation();
    findCircle(); //axios  
  };

  //   
  const dragLocation = () => {
    document.querySelector(".dragTarget").style.display = "block";
    findDragLoc(); //axios  
  };

  //   
  function displayMarker(locPosition, message) {
    //  
    var marker = new kakao.maps.Marker({
      map: map,
      position: locPosition,
    });

    //     
    setCurrentLocationMarker(marker);

    //    
    map.setCenter(locPosition);
  }

  return (
    <>
      <div
        className="relative"
        id="map"
        style={{
          width: "500px",
          height: "calc(100vh - 65px)",
          // position: "relative",
        }}
      >
        <div className="dragTarget">
          <i className="fa-solid fa-location-crosshairs"></i>
        </div>
        <MainSlider indexPet={indexPet} />
        <div className="flex flex-col absolute bottom-[20px] right-[20px] z-50 gap-2">
          <button
            className="bg-ye-600 px-3 py-1 rounded-full flex items-center gap-1"
            onClick={myLocation}
          >
             
            <i className="fa-solid fa-location-crosshairs"></i>
          </button>
          <button
            className="bg-ye-600 px-3 py-1 rounded-full flex items-center gap-1"
            onClick={dragLocation}
          >
             
            <i class="fa-regular fa-compass"></i>
          </button>
        </div>
      </div>
    </>
  );
}

export default Kakao_main;
