import React, { useEffect, useState } from "react";
import ButtonBl from "../components/ButtonBl";
import ButtonYe from "../components/ButtonYe";
import { Link, useNavigate } from "react-router-dom";
import TextFieldLine from "../components/TextField";
import CheckCircleButton from "../components/CheckCircleButton";
// import axiosInstance from "../utils/axios";
import { createCircle } from "../store/thunkFunctions";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import Kakao_point from "../kakaoMap/Kakao_point";
import axiosInstance from "../utils/axios";
import { format, formatDate, set } from "date-fns";
import Kakao_start_point from "../kakaoMap/Kakao_start_point";
// import { Box, FormControl, MenuItem, Select } from "@mui/material";
// import SelectButton from "../components/SelectButton";
// import { SelectUnstyled, OptionUnstyled } from "@mui/base";
// import { styled } from "@mui/system";

const peoplesOptions = [
  { key: 1, value: 2, display: "2" },
  { key: 2, value: 3, display: "3" },
  { key: 3, value: 4, display: "4" },
  { key: 4, value: 5, display: "5" },
];

const usingTimeOptions = [
  { key: 1, value: 30, display: "30" },
  { key: 2, value: 45, display: "45" },
  { key: 3, value: 60, display: "1" },
  { key: 4, value: 75, display: "1 15" },
  { key: 5, value: 90, display: "1 30" },
  { key: 6, value: 105, display: "1 45" },
  { key: 7, value: 120, display: "2" },
];

//            circle/new post // address coord 

function CreateCCPage() {
  const { kakao } = window;
  const [endAddress, setEndAddress] = useState(""); //
  const [startAddress, setStartAddress] = useState(""); //
  const [endCoord, setEndCoord] = useState([]);
  const [coordinates, setCoordinates] = useState([]);
  const loginState = useSelector((state) => {
    return state.user.userData.user.id;
  });

  const [startTime, setStartTime] = useState("");
  const [startDate, setStartDate] = useState("");
  const [usingTime, setUsingTime] = useState([
    { key: 1, value: " 30" },
    { key: 2, value: "45" },
    { key: 3, value: "1" },
    { key: 4, value: "1 15" },
    { key: 5, value: "1 30" },
    { key: 6, value: "1 45" },
    { key: 7, value: "2" },
  ]);
  const [peoples, setPeoples] = useState(null);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    // watch,
  } = useForm({ mode: "all" });
  const dispatch = useDispatch();

  const [startshowBox, setStartshowBox] = useState(false); //  
  const [endshowBox, setEndshowBox] = useState(false); //  
  const [checkCircle, setCheckCircle] = useState(false); // 
  const [newCCInfo, setnewCCInfo] = useState({
    user: loginState,
    name: "",
    text: "",
    startLoc: "",
    endLoc: "",
    startDate: "",
    startTime: "",
    usingTime: "",
    peoples: "",
  });
  useEffect(() => {
    setnewCCInfo((prevState) => ({
      ...prevState,
      startTime: startTime,
      startDate: startDate,
      usingTime: usingTime,
      peoples: peoples,
      startLoc: { coordinates: coordinates },
      endLoc: { endCoordinates: endCoord },
      startAdd: startAddress,
    }));
  }, [
    startTime,
    usingTime,
    peoples,
    startDate,
    coordinates,
    endCoord,
    startAddress,
  ]);

  //  
  const startToggleBox = () => {
    setStartshowBox(!startshowBox);
  };

  //  
  const endToggleBox = () => {
    setEndshowBox(!endshowBox);
  };

  // 
  function handleCheckCircle(result) {
    setCheckCircle(result);
  }

  // useNavigate
  const navigate = useNavigate();

  function handleChangeValue(e) {
    const { name, value } = e.target;
    if (name && value !== undefined) {
      setnewCCInfo((prevState) => ({
        ...prevState,
        [name]: value,
      }));
    }
  }

  async function onSubmit(test) {
    console.log(test);
    const body = {
      ...newCCInfo,

      startTime: new Date(
        newCCInfo.startDate + "T" + newCCInfo.startTime + ":00.000Z"
      ),
      usingTime: new Date(Number(newCCInfo.usingTime)),
    };

    console.log("sending Data:::", body);

    try {
      const res = await axiosInstance.post(`/circles/new/${loginState}`, body);
      console.log(" ", res.data);

      toast.success("  .", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: true,

        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });

      reset();

      navigate("/circles");
    } catch (error) {
      //    
      console.log(" ", error.message);

      toast.error(
        <div>
           .!!
          <br />
             
        </div>,
        {
          position: "bottom-right",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        }
      );
    }
  }

  const validationRules = {
    name: {
      required: {
        value: true,
        message: " .",
      },

      maxLength: {
        value: 20,
        message: " 20 .",
      },
    },
    text: {
      required: " .",
      maxLength: {
        value: 100,
        message: "100   .",
      },
    },
    startDate: {
      required: "  .",
    },
    startTime: {
      required: "  .",
    },
    usingTime: {
      required: "  .",
    },
    peoples: {
      required: "  .",
    },
  };

  //    
  useEffect(() => {
    if (startAddress) {
      const geocoder = new kakao.maps.services.Geocoder();
      geocoder.addressSearch(startAddress, (result, status) => {
        if (status === kakao.maps.services.Status.OK) {
          const coordinates = [
            Number(result[0].road_address.x),
            Number(result[0].road_address.y),
          ];
          setCoordinates(coordinates);
          console.log("Start Coordinates:", coordinates); //   

          setnewCCInfo((prevState) => ({
            ...prevState,
            startLoc: startAddress,
          }));
          startToggleBox();
        }
      });
    }
  }, [startAddress]);

  //   
  function handleDragLoc(coordinates) {
    console.log("   ");
    setCoordinates(coordinates);
  }

  //    
  useEffect(() => {
    if (endAddress) {
      const geocoder = new kakao.maps.services.Geocoder();
      geocoder.addressSearch(endAddress, (result, status) => {
        if (status === kakao.maps.services.Status.OK) {
          const coordinates = [
            Number(result[0].road_address.x),
            Number(result[0].road_address.y),
          ];
          setEndCoord(coordinates);
          console.log("End Coordinates:", coordinates); //   
          setnewCCInfo((prevState) => ({
            ...prevState,
            endLoc: endAddress,
          }));
          endToggleBox();
        }
      });
    }
  }, [endAddress]);

  // 
  useEffect(() => {
    //     
    if (!window.daum) {
      const script = document.createElement("script");
      script.src =
        "https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js";
      script.async = true;
      // script.onload = () => {
      //   openPostcode();
      // };
      document.head.appendChild(script);
    } else {
      // openPostcode();
    }
  }, []);

  const openPostcode = () => {
    new window.daum.Postcode({
      oncomplete: function (data) {
        //         .
        //      .
        console.log(data.address); //     .
        setEndAddress(data.address);
      },
    }).open();
  };
  const startOpenPostcode = () => {
    new window.daum.Postcode({
      oncomplete: function (data) {
        //         .
        //      .
        console.log(data.address); //     .
        setStartAddress(data.address);
      },
    }).open();
  };

  const checkDate = (date) => {
    if (
      new Date((date.startDate + "T" + date.startTime + ":00.000Z").getTime()) >
      new Date.now()
    ) {
      return false;
    } else handleChangeValue();
  };

  const handleTextFieldClick = () => {
    return openPostcode(); // TextFieldLine      
  };
  const starthandleTextFieldClick = () => {
    startOpenPostcode(); // TextFieldLine      
  };

  return (
    <>
      <div className="bg-white px-12 border-[1px] border-da-100 ">
        <form
          className="pt-[90px] pb-[100px]"
          onSubmit={handleSubmit(onSubmit)}
        >
          {/* <form className="pt-[90px] pb-[100px]"> */}
          {/*   */}
          <div className="mt-5">
            <div className="text-center border rounded-md  mb-5 px-4 py-2 text-[15px]">
              <span className="text-red-500 text-[17px] mr-[2px]">
                 
              </span>
                
              <br />
              ?
            </div>
            {/* <div className="flex justify-around items-center mb-4 px-4 py-2 gap-7"> */}
            <CheckCircleButton
              handleCheckCircle={handleCheckCircle}
              checkCircle={checkCircle}
            />
          </div>
          <div>
            {/*  start */}
            <div className="flex flex-col mb-6 pt-5">
              <label
                className={
                  checkCircle
                    ? `w-[100px] mb-3 nanumBold text-[15px] text-da-400`
                    : `w-[100px] mb-4 nanumBold text-[15px] text-da-500`
                }
                htmlFor="title"
              >
                
              </label>
              <TextFieldLine
                {...register("name", validationRules.name)}
                required
                disabled={!checkCircle}
                id="name"
                name="name"
                label=""
                fullWidth
                onChange={handleChangeValue}
                value={newCCInfo.name}
                // error={!!errors.name}
                // helperText={errors.name?.message}
              />
              {checkCircle && errors.name && (
                <div className="nanum text-red-500 text-xs mt-2 ml-1">
                  {errors.name.message}
                </div>
              )}
            </div>
            {/* end */}
            {/*  */}
            <textarea
              {...register("text", validationRules.text)}
              placeholder=" ."
              id="text"
              name="text"
              className={
                checkCircle
                  ? `bg-white rounded-[4px] w-full h-[160px] text-justify px-4 py-2 border border-da-500 nanum text-[15px] hover:border-ye-800 focus:border-ye-600 focus:border-2 outline-none`
                  : `bg-gray-200 rounded-[4px] w-full h-[160px] text-justify px-4 py-2 border  text-da-500 nanum text-[15px] `
              }
              value={newCCInfo.text}
              onChange={handleChangeValue}
              disabled={checkCircle ? false : true}
              // error={!!errors.text}
              // helperText={errors.text?.message}
              // className="bg-gray-200 rounded-md w-full h-[100px] text-justify mb-4 px-4 py-2"
            />
            {checkCircle && errors.text && (
              <div className="nanum text-red-500 text-xs mt-1 ml-1">
                {errors.text.message}
              </div>
            )}
          </div>

          {/* , */}
          <div>
            <div>
              <div>
                <label
                  htmlFor="startPoint"
                  className={
                    checkCircle
                      ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                      : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500`
                  }
                >
                  {/* <label htmlFor="startPoint" className=" mb-4 flex gap-2 "> */}
                  <i class="fa-regular fa-flag mt-1"></i>
                  
                </label>

                <TextFieldLine
                  onChange={handleChangeValue}
                  onClick={starthandleTextFieldClick}
                  required
                  id="startLoc"
                  name="startLoc"
                  label=""
                  fullWidth
                  readOnly
                  className="cursor-pointer"
                  disabled={!checkCircle}
                  // value={newCCInfo.startLoc}
                  value={startAddress}
                />
              </div>
              {/* <div className="relative h-[37px]">
                {checkCircle && (
                  <img
                    src="/images/plusglass_icon.svg"
                    alt=" "
                    className="block absolute left-[369px] bottom-[57px] cursor-pointer"
                    onClick={starthandleTextFieldClick}
                    disabled={!checkCircle}
                  />
                )}
              </div> */}
              {/* <img
                src="/images/plusglass_icon.svg"
                alt=" "
                className="block relative  left-[370px] bottom-[37px] cursor-pointer"
                onClick={starthandleTextFieldClick}
              /> */}
              {startshowBox && (
                <div className="bg-gray-100 px-4 py-2 mb-4 border-2 rounded-md z-10 startstart">
                  <Kakao_start_point
                    handleDragLoc={handleDragLoc}
                    startCoord={coordinates && coordinates}
                    startToggleBox={startToggleBox}
                  />
                </div>
              )}
            </div>
            <div className="mb-4">
              <div>
                <label
                  htmlFor="endPoint"
                  className={
                    checkCircle
                      ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                      : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500`
                  }
                >
                  {/* <label htmlFor="endPoint" className=" mb-4 flex gap-2 "> */}
                  <i class="fa-solid fa-flag mt-1"></i>
                  
                </label>

                <TextFieldLine
                  onClick={handleTextFieldClick}
                  onChange={handleChangeValue}
                  required
                  id="endLoc"
                  name="endLoc"
                  label=""
                  fullWidth
                  // readOnly
                  className="cursor-pointer"
                  // value={newCCInfo.endLoc}
                  value={endAddress}
                  disabled={!checkCircle}
                />
              </div>
              {/* <div className="relative h-[37px]">
                {checkCircle && (
                  <img
                    src="/images/plusglass_icon.svg"
                    alt=" "
                    className="block absolute left-[369px] bottom-[
                      57px] cursor-pointer"
                    onClick={handleTextFieldClick}
                    disabled={!checkCircle}
                  />
                )}
              </div> */}
              {/* <img
                src="/images/plusglass_icon.svg"
                alt=" "
                className="block relative  left-[370px] bottom-[37px] cursor-pointer"
                // onClick={endToggleBox}
                onClick={handleTextFieldClick}
              /> */}
              {endshowBox && (
                <div className=" bg-gray-100 p-1  mb-4 border-2 rounded-md ">
                  <Kakao_point
                    endCoord={endCoord && endCoord}
                    endToggleBox={endToggleBox}
                    className="z-10"
                  />
                </div>
              )}
            </div>
            <div className="mb-6">
              <label
                htmlFor="startDate"
                className={
                  checkCircle
                    ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                    : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500`
                }
              >
                {/* <label htmlFor="startTime" className=" mb-4 flex gap-2 "> */}
                <i class="fa-regular fa-calendar-check mt-[3px]"></i>
                 
              </label>

              <TextFieldLine
                {...register("startDate", validationRules.startDate)}
                onChange={(e) => setStartDate(e.target.value)}
                required
                id="startDate"
                name="startDate"
                fullWidth
                type="date"
                // readOnly={false} // readOnly     false 
                readOnly // readOnly     false 
                className="cursor-pointer"
                disabled={!checkCircle} //  
                value={newCCInfo.startDate}
                // error={!!errors.startDate}
                // helperText={errors.startDate?.message}
              />
              {checkCircle && errors.startDate && (
                <div className="nanum text-red-500 text-xs mt-2 ml-1">
                  {errors.startDate.message}
                </div>
              )}
            </div>
            <div className="mb-4">
              <label
                htmlFor="startTime"
                className={
                  checkCircle
                    ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                    : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500`
                }
              >
                {/* <label htmlFor="startTime" className=" mb-4 flex gap-2 "> */}
                <i class="fa-regular fa-clock mt-[3px]"></i>
                 
              </label>

              <input
                {...register("startTime", validationRules.startTime)}
                required
                // type="datetime-local"
                type="time"
                id="startTime"
                name="startTime"
                className={
                  checkCircle
                    ? `w-full mb-2 py-2 px-4 border rounded-md `
                    : `w-full mb-2 py-2 px-4 border rounded-md text-da-500`
                }
                onChange={(e) => setStartTime(e.target.value)}
                disabled={!checkCircle}
                value={newCCInfo.startTime}
                // error={!!errors.startTime}
                // helperText={errors.startTime?.message}
              />
              {checkCircle && errors.startTime && (
                <div className="nanum text-red-500 text-xs mt-2 ml-1">
                  {errors.startTime.message}
                </div>
              )}
            </div>

            <div>
              <label
                htmlFor="usingTime"
                className={
                  checkCircle
                    ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                    : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500`
                }
              >
                {/* <label htmlFor="usingTime" className="mb-4 flex gap-2"> */}
                <i class="fa-solid fa-clock mt-[3px]"></i>
                 
              </label>

              <select
                {...register("usingTime", validationRules.usingTime)}
                id="usingTime"
                name="usingTime"
                // className={
                //   checkCircle
                //     ? `w-full mb-6 py-2 px-4 border rounded-md`
                //     : `w-full mb-6 py-2 px-4 border rounded-md text-da-500`
                // }
                className="w-full border px-4 py- 2 mb-4 rounded-md block h-[60px] cursor-pointer nanum text-[15px] text-da-300 border-[#e0e3e7] hover:border-ye-800 focus:border-ye-600 focus:border-2 outline-none"
                disabled={!checkCircle}
                // onChange={(e) => setUsingTime(e.target.value)}
                onChange={handleChangeValue}
                value={newCCInfo.usingTime}
                error={!!errors.usingTime}
                helperText={errors.usingTime?.message}
              >
                <option value="">  .</option>
                {usingTimeOptions.map((option) => (
                  <option key={option.key} value={option.value * 1000 * 60}>
                    {option.display}
                  </option>
                ))}
              </select>
              {checkCircle && errors.usingTime && (
                <div className="nanum text-red-500 text-xs pl-2 pb-1">
                  {errors.usingTime.message}
                </div>
              )}
            </div>

            <div className="mt-4">
              <label
                htmlFor="peoples"
                className={
                  checkCircle
                    ? `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-400`
                    : `mb-3 flex gap-2 mt-8 nanumBold text-[15px] text-da-500 `
                }
              >
                <i class="fa-solid fa-user-group mt-[3px]"></i>
                  
              </label>

              <select
                {...register("peoples", validationRules.peoples)}
                id="peoples"
                name="peoples"
                className="w-full px-4 py-2 mb-4 border rounded-md  h-[60px] cursor-pointer nanum text-[15px] text-da-300 border-[#e0e3e7] hover:border-ye-800 focus:border-ye-600 focus:border-2 outline-none"
                disabled={!checkCircle}
                // className={
                //   checkCircle
                //     ? `w-full mb-6 py-2 px-4 border rounded-md`
                //     : `w-full mb-6 py-2 px-4 border rounded-md text-da-500`
                // }
                onChange={handleChangeValue}
                // onChange={(e) => setPeoples(e.target.value)}
                value={newCCInfo.peoples}
                // error={!!errors.peoples}
                // helperText={errors.peoples?.message}
              >
                <option value="">  .</option>
                {peoplesOptions.map((option) => (
                  <option key={option.key} value={option.value}>
                    {option.display}
                  </option>
                ))}
              </select>
              {checkCircle && errors.peoples && (
                <div className="nanum text-red-500 text-xs pl-2 pb-1">
                  {errors.peoples.message}
                </div>
              )}
            </div>
          </div>
          {/* , */}
          <div
            className="flex justify-center items-center gap-10 mt-16"
            disabled={!checkCircle}
          >
            <Link to="/circles">
              <ButtonBl></ButtonBl>
              {/* <button></button> */}
            </Link>
            {/* <Link to="/circles"> */}
            <ButtonYe type="submit" disabled={checkCircle ? false : true}>
              
            </ButtonYe>
            {/* <ButtonYe type="submit"></ButtonYe> */}
            {/* </Link> */}
          </div>
        </form>
      </div>
      {/*     ,   */}
    </>
  );
}

export default CreateCCPage;
